.rule-set-container {
  padding: 10px;
}
.rule-set-container md-icon {
  outline: none;
  margin-left: 5px;
  margin-right: 5px;
}
.rule-set-container md-icon[disabled] {
  color: lightgray;
}
.rule-set-container md-icon.add-rule,
.rule-set-container md-icon.add-rule-set {
  color: blue;
  cursor: pointer;
}
.rule-set-container md-icon.remove-rule-set {
  cursor: pointer;
  color: red;
}
.rule-set-container md-icon[disabled] {
  color: lightgray;
}
.rule-set-container div.read-only-message {
  background-color: lightgrey;
  border-radius: 25px;
  padding: 3px;
  text-align: center;
}
.rule-set-container .md-button.add-rule,
.rule-set-container .md-button.add-rule-set,
.rule-set-container .md-button.remove-rule-set {
  margin-left: 5px;
  margin-right: 5px;
}
.rule-set-container .md-button.remove-rule-set {
  color: red;
  max-width: 30px;
  min-width: 30px;
}
.rule-set-container .child-rule-container {
  margin-left: 2%;
  margin-top: 5px;
  margin-bottom: 5px;
}
.rule-set-container .child-rule-container.container-outline {
  border: 2px solid lightgray;
}
.rule-set-container .rule-connector {
  position: relative;
}
.rule-set-container .rule-connector::before {
  top: -5px;
  border-width: 0 0 2px 2px;
}
.rule-set-container .rule-connector::after {
  border-width: 0 0 0 2px;
  top: 50%;
}
.rule-set-container .rule-connector::before,
.rule-set-container .rule-connector::after {
  content: '';
  left: -12px;
  border-color: #CCC;
  border-style: solid;
  width: 9px;
  height: calc(50% + 6px);
  position: absolute;
}
.rule-set-container .rule-connector:last-child::after {
  content: none;
}
