.rule-container md-input-container {
  margin: 10px 0;
}
.rule-container md-input-container.class-type {
  margin-top: 20px;
}
.rule-container md-autocomplete[disabled] {
  background: inherit;
}
.rule-container md-icon.remove-rule {
  cursor: pointer;
  color: red;
}
.rule-container .md-button.remove-rule {
  color: red;
  max-width: 30px;
  min-width: 30px;
}
.rule-container md-autocomplete-wrap {
  box-shadow: none;
}
.rule-container .md-errors-spacer {
  min-height: 0px;
}
